	module.exports = {"data":{"markdownRemark":{"html":"<p>Featuring super-lovely production and a cast of all stars, <a href=\"http://ted.com/\">TEDTalks</a> emerges from its walled garden with a serious web presence.</p>\n<p>\"Each year, TED hosts some of the world's most fascinating people: Trusted voices and convention-breaking mavericks, icons and geniuses. The talks they deliver have had had such a great impact, we thought they deserved a wider audience. So now, for the first time, we're sharing them with the world at large.\" </p>\n<p>via <a href=\"http://37signals.com/svn/archives2/sunspots_the_fourth_wall_edition.php\">Signal vs. Noise</a> and <a href=\"http://www.ethanzuckerman.com/blog/?cat=18\">Ethan Zuckeman</a> and friends at <a href=\"http://www.activelivingbydesign.org/\">Active Living By Design</a> (who kicked our geeky asses at Ultimate last week).</p>","frontmatter":{"created_at":"July 11, 2006","path":"/2006/07/11/ted-reaches-out-to-the-proles","title":"TED Reaches out to the Proles","tags":["ted","video"]}}},"pathContext":{}}

